





















































import { Component, Vue, Prop } from 'vue-property-decorator';

@Component
export default class PopDialog extends Vue {
  @Prop({
    type: String,
    default: 'inform'
  })
  dialogType!: string;

  @Prop({
    type: Boolean,
    default: false
  })
  showFlag!: Boolean;

  @Prop({
    type: Boolean,
    default: false
  })
  showCloseFlag!: Boolean;

  @Prop({
    type: Boolean,
    default: false
  })
  clickBgCloseFlag!: Boolean;

  @Prop({
    type: String,
    default: ''
  })
  title!: string;

  @Prop({
    type: Boolean, // 
    default: false //   Object default: ()=>({}) 
  })
  centerStyle!: Boolean;

  @Prop({
    type: String,
    default: ''
  })
  cancelbuttonText!: string;

  @Prop({
    type: String, // 
    default: '' //   Object default: ()=>({}) 
  })
  buttonText!: string;

  @Prop({
    type: Boolean,
    default: false
  })
  sureButtonDisabled!: Boolean;

  @Prop({
    type: String,
    default: 'primary'
  })
  sureButtonType!: string;

  @Prop({
    type: Number // 
  })
  width!: number;

  @Prop({
    type: Number // 
  })
  height!: number;

  clickBtn(type: string) {
    if (type === 'sure') {
      this.$emit('sureClick');
    }
    if (type === 'cancel') {
      this.$emit('cancelClick');
    }
  }

  clickBg() {
    if (this.clickBgCloseFlag) {
      this.hideIt();
    }
  }

  hideIt() {
    this.$emit('update:showFlag', false);
  }
}
